import Control from "../controll";
import sectionStyles from './sectionStyles.css';
import {projectData} from "./app";

type datatype={
	title:string,
	arrayOfData:string[],
	styles?:string
}
type sitesData={

}
export class Section extends Control {
	constructor(parentNode: HTMLElement,data:datatype) {
		super(parentNode, 'div', sectionStyles.sectionSt);

		const title= new Control(this.node, 'h3',sectionStyles.sectionTitle,data.title)
		const skillsUl = new Control(this.node, 'ul',sectionStyles.sectionUl)
		//if(data.styles){
		// data.styles=='skills' && (skillsUl.node.setAttribute('class'
		// 	,sectionStyles.skillsUl))
		//}
		data.arrayOfData.forEach((sk,ind) => {
			const listItem = new Control(skillsUl.node, 'li',
				sectionStyles.sectiinLi,
				data.styles==='skills' && ind<data.arrayOfData.length-1
					? sk+`-`
					:sk)
		})

	}
}
export class ProjectsSection extends Control{
	constructor(parentNode: HTMLElement, data: { title: string; arrayOfData: Record<string, projectData[]> }) {
		super(parentNode,'div',sectionStyles.sectionSt);
	new Control(this.node,'h3',sectionStyles.sectionTitle,
		'Projects')
		Object.entries(data.arrayOfData).forEach(el=>{
			const title=new Control(this.node,'h3',sectionStyles.sectionSubTitle,el[0])
			const elementWrapper=new Control(this.node,'ul',sectionStyles.sectionUl)
			el[1].forEach(s=>{
				const siteItem = new Control(elementWrapper.node,'li',
					sectionStyles.descriptionListElement)
				const titleWrap=new Control(siteItem.node,'span')
				const siteLink=new Control(titleWrap.node,'img')
				const spanTitle=new Control(titleWrap.node,'span',
					sectionStyles.siteTitle,s.site)

				siteLink.node.setAttribute('src','./public/assets/image/githubIcon.svg')
			const description=new Control(siteItem.node,'span',
				'',` (${s.descr})`)
			})
			console.log(el[1])
		})
	}
}